@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    background-color: var(--primary);
    height: auto;
    padding: 16px 0;
}

.header-menu{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}

.header-Item {
    text-align: center;
    border: none;
    border-radius: 26px;
    background: #2c2e3e;
    color: var(--white);
    padding: 12px;
    transition: all 1s;
    width: 250px;
}

.header-Item:hover{
    background-color: var(--secondary);
    cursor: pointer;
}

.headerItem-icon{
    height: 16px;
}

.hidden {
    margin-right: 10%;
    display: none;
}
.hidden:hover {
    cursor: pointer;
    margin-right: 10%;
    opacity: 0.5;
}


@media (min-width: 320px) {
    .header{
        display: flex;
        justify-content: flex-end;
        height: 70px;
    }

    .header-menu{
        display: flex;
        left: 100%;
        overflow-y: auto;
        position: fixed;
        top: 50px;
        transition: left 0.3s;
        width: 100%;
    }

    .active {
        flex-direction: column;
        align-items: center;
        display: block;
        position: absolute;
        left: 0;
        top: 50px;
        z-index: 1;
    }

    .hidden {
        display: inline;
    }

    .header-Item {
        margin: 0 auto;
    }
    
    .hidden-img {
        -webkit-animation-duration: 0.5s;
                animation-duration: 0.5s;
        -webkit-animation-name: slidein;
                animation-name: slidein;
        margin-top: 40%;
        height: 20px;
    }
}

@media (min-width: 768px) {
    .header{
        align-items: center;
        height: 110px;
    }

    .header-menu{
        display: flex;
        left: 0;
        top: 10px;
        width: 100%;
    }

    .hidden {
        display: none;
    }
    .header-Item {
        font-size: 1.6rem;
        width: 300px;
    }
}

@media (min-width: 1024px) {
    .header{
        height: 70px;
    }
    .header-Item {
        width: 200px;
    }
}

@-webkit-keyframes slidein {
    0% {
        opacity: 0;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }

    50% {
        opacity: 0.5;
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
    }
  
    100% {
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
  }

@keyframes slidein {
    0% {
        opacity: 0;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }

    50% {
        opacity: 0.5;
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
    }
  
    100% {
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
  }
.sideBar {
    background-color: var(--secondary);
    color: #F7F6F2;
    display: flex;
    flex-direction: column;
    font-size: 17px;
    height: calc(100%);
    overflow-y: auto;
    padding: 0;
    position: absolute;
}

@media (min-width: 320px) {
    .sideBar {
        margin-top: 0;
        height: calc(100vh - 110px);
        width: 100px;
    }
}

@media (min-width: 768px) {
    .sideBar {
        /* height: calc(100vh - 110px); */
        width: 230px;
    }
}

.sidebarElement {
    align-items: center;
    border: 0;
    border-radius: 20px;
    background: linear-gradient(45deg, #383b51, #2f3244);
    box-shadow:  5px -5px 10px #2b2d3e,
                -5px 5px 10px #3d415a;
    color: var(--white);
    cursor: pointer;
    display: flex;
    font-family: 'Poppins', sans-serif;
    font-size: 1.5rem;
    justify-content: space-between;
    margin: 8px;
    padding: 16px;
    text-decoration: none;
    transition: all 1s;
}

.iconTeam {
    height: 24px;
}

.sidebarElement:hover {
    background: var(--primary);
    /* Usar este efecto para una clase selecionada
    background: #D90D32;
    box-shadow: inset 7px 7px 14px #b60b2a,
                inset -7px -7px 14px #fc0f3a; */
}

@media (min-width: 320px) {
    #standings p{
        font-size: 1rem;
        align-self: center;
        display: block;
    }

    .teamTitle{
        display: none;
    }
}

@media (min-width: 768px) {
    .teamTitle{
        font-size: 1.7rem;
        display: inline;
    }
    #standings p{
        font-size: 1.7rem;
    }
}
.standings-container {
    margin-left: 250px;
}

.standings-team-icon{
    height: 2rem;
}

@media (min-width: 320px){
    .standings-container{
        font-size: 1.3rem;
        height: calc(100vh - 110px);
        margin-left: 100px;
        overflow: auto;
        width: calc(100vw - 100px);
        padding: 5px;
    }
    .standings-container table {
        width: 100%;
    }

    table, td {
        border: 1px solid #1e1d408a;
        border-collapse: collapse;
        padding: 2px;
    }

    .standings-container .standings-table .standings-team-position {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .standings-container table .standings-team-info {
        text-align: center;
    }

}

@media (min-width: 768px) {
    .standings-container{
        font-size: 1.7rem;
        height: calc(100vh - 140px);
        margin-left: 230px;
        width: calc(100vw - 230px);
        padding: 20px;
    }
    .standings-team-icon{
        height: 3rem;
    }

    .standings-container table {
        width: 90%;
        margin: 0 auto;
    }
}

@media (min-width: 1024px) {
    .standings-container{
        font-size: 2rem;
        height: calc(100vh - 100px);
        padding: 20px;
    }
}

.score{
    border-radius: 50px;
    background: var(--white);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 20px;
    color: var(--primary);
    text-align: center;
    margin: 10px;
    padding: 5px;
    width: 45%;
}

@media (min-width: 320px) {
    .score{
        width: 100%;
    }

    .matches .score p {
        font-size: 1.5rem;
    }
}

@media (min-width: 768px) {
    .score{
        width: 45%;
    }

    .matches .score p {
        font-size: 1.7rem;
    }
}
.matches{
    color: var(--white);
    display: flex;
    font-size: 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 25%;
    padding: 10px 16px;
    width: 75%;
    height: 90vh;
    overflow: auto;
}


@media (min-width: 320px) {
    .matches{
        margin-left: 100px;
        height: calc(100vh - 110px);
        width: calc(100vw - 100px);
    }
}

@media (min-width: 768px) {
    .matches{
        margin-left: 230px;
        height: calc(100vh - 140px);
        width: calc(100vw - 230px);
    }
}
.loadingContainer{
    color: #000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 230px);
}

.loadingContainer .text-charging {
    display: inline;
    -webkit-animation-name: loadign;
            animation-name: loadign;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@media (min-width: 320px) {
    .loadingContainer{
        font-size: 2rem;
        margin-left: 100px;
        width: calc(100vw - 100px);
    }
}

@media (min-width: 768px) {
    .loadingContainer{
        font-size: 5rem;
        margin-left: 230px;
        width: calc(100vw - 230px);
    }
    
}

@-webkit-keyframes loadign {
    0% {
        opacity: 0;
    }
    25%{
        opacity: 0.4;
    }
    50%{
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
  }

@keyframes loadign {
    0% {
        opacity: 0;
    }
    25%{
        opacity: 0.4;
    }
    50%{
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
  }

  @-webkit-keyframes donut-spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @keyframes donut-spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  .donut {
    display: inline-block;
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-left-color: var(--red);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    -webkit-animation: donut-spin 1.2s linear infinite;
            animation: donut-spin 1.2s linear infinite;
  }
.footer {
    background-color: var(--primary);
    bottom: -10px;
    color: var(--white);
    font-size: 1.5rem;
    height: 50px;
    padding: 10px;
    position: fixed;
    text-align: right;
    width: 100%;
}

.footer p a {
    color: var(--red);
    text-decoration: none;
    padding-right: 10px;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #f6f6f6;
    background-color: var(--white);
    font-family: 'Poppins', sans-serif;
}

html { font-size: 62.5%; }

:root{
    --white: #f6f6f6;
    --red: #ee2b47;
    --primary: #2c2e3e;
    --secondary: #34374c;
}

::-webkit-scrollbar{
    width: 12px;
}

::-webkit-scrollbar-track{
    background: #2c2e3e;
    background: var(--primary);
    border-radius: 8px;
}

::-webkit-scrollbar-thumb{
    background: #ee2b47;
    background: var(--red);
    border-radius: 8px;
}
